.container {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.logo_link {
  height: 40px;
}

.nav {
  margin: 0;
}

.nav__links {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__links li:last-child a {
  margin-right: 0;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  height: 60px;
  margin-right: 20px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: " ";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.card__stars {
  justify-content: space-between;
  align-items: flex-end;
  padding: 16px 0;
  line-height: 14px;
  display: flex;
}

.card__stars-reviews {
  color: #060b35;
  margin: 0;
  font-size: 10px;
}

.stars {
  justify-content: space-between;
  width: 96px;
  padding-right: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--4 .stars__star:nth-last-child(n+2) {
  background-image: url("star-active.edda4c43.svg");
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 16px;
}

.card__product-img {
  justify-content: center;
  padding: 16px 0;
  display: flex;
}

.card__img {
  width: 160px;
  height: 134px;
}

.card__name {
  color: #060b35;
  margin: 24px 0 4px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin: 8px 0 16px;
  display: flex;
}

.card__price-txt {
  color: #616070;
  margin: 0;
}

.card__price-sum {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button-buy {
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  box-sizing: border-box;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  display: inline-block;
}

.card__button-buy:hover {
  color: #00acdc;
  background-color: #fff;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

body {
  box-sizing: border-box;
  margin: 0;
}

.cards {
  place-content: center;
  gap: 46px 48px;
  display: grid;
}

.cards-container--position {
  padding: 50px 40px;
}

@media (width >= 488px) {
  .cards {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .cards {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .cards {
    grid-template-columns: repeat(4, 200px);
  }
}
